body {
  background-color: #eee;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  background-color: inherit;
  display: inline-block;
  margin-left: 5px;
  border: 1px inset grey;
  font: inherit;
  text-align: center;
  border: thin solid rgba(0, 0, 0, 0); /* Transparent */
  border-bottom: 2px solid black;
}

input:focus {
  border: thin dotted grey;
  border-bottom: 2px solid black;
}

button {
  background-color: #ffdd54;
  border-radius: 5px;
  color: #2020fb;
  border: none;
  padding: 5px;
  font-weight: bold;
}

button:hover {
  transition: -webkit-transform 0.1s linear;
  transition: transform 0.1s linear;
  transition: transform 0.1s linear, -webkit-transform 0.1s linear;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.flex {
  display: -webkit-flex;
  display: flex;
}

.flex-column {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.flex-wrap {
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.content-center {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.content-space-between {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.capital {
  text-transform: capitalize;
}

.center-text {
  text-align: center;
}

.hidden {
  visibility: hidden;
}

#btn-go {
  margin-left: 1em;
  font-size: 16px;
}

#pokeball-img {
  width: 20px;
  height: 20px;
  position: relative;
  top: 5px;
  left: 5px;
}

nav ul {
  list-style: none;
  margin-bottom: 0;
  cursor: pointer;
}

nav li {
  display: inline-block;
}

nav li:hover,
.active-link {
  background-color: #eee;
  color: #e74c3c;
}

nav a {
  text-decoration: none;
  color: inherit;
  display: block;
  padding: 0.5em 1em;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #e74c3c;
  min-height: 20vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-content {
  padding-bottom: 3em;
}

.App-footer {
  box-sizing: border-box;
  background-color: #eee;
  font-size: 12px;
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 3em;
  padding: 10px 0;
  z-index: 100; /* Footer always on top */
}

.App-link {
  color: #61dafb;
}

.pokemon-header {
  margin: 1em auto;
  border: 2px solid black;
  border-radius: 1em;
  width: 50%;
  padding: 5px;
  background: linear-gradient(
    to bottom,
    #e74c3c 45%,
    black 45%,
    black 50%,
    #eee 50%
  );
  font-size: calc(15px + 1vmin);
}

.pokemon-header-text {
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  font-weight: bold;
}

.sprite-container {
  border: 2px solid black;
  border-radius: 100%;
  background-color: #eee;
  min-width: 125px;
  min-height: 125px;

  max-width: 250px;
  max-height: 250px;

  /* Use flex to center image */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

